






































import Vue from 'vue';
import AccountApi from '@/apis/AccountApi';
import { Role } from '@/apis/models/RoleModel';
import navbar from '@/components/navbar.vue';

export default Vue.extend({
  components: {
    navbar,
  },
  data() {
    return {
      account: '',
      hasAccount: true,
      password: '',
      hasPassword: true,
      loginMessage: '',
    };
  },
  methods: {
    reset() {
      this.account = '';
      this.password = '';
    },
    loginButton() {
      if (this.account !== '' && this.password !== '') {
        this.hasPassword = true;
        this.hasAccount = true;
        this.handleLogin();
      } else {
        if (this.account === '') {
          this.hasAccount = false;
        } else {
          this.hasAccount = true;
        }
        if (this.password === '') {
          this.hasPassword = false;
        } else {
          this.hasPassword = true;
        }
      }
    },
    async handleLogin() {
      try {
        const { profile, auth } = await AccountApi.login(this.account, this.password);
        this.$cookies.set('token', auth.token);
        let homepageRoute = '/user';
        if (profile.roles.includes(Role.ADMIN) || profile.roles.includes(Role.MANAGER)) {
          homepageRoute = '/manager/workspace';
        }
        if (profile.roles.includes(Role.QUIZ_EDITOR)) {
          homepageRoute = '/manager/workspace/quizgroups';
        }
        if (profile.roles.includes(Role.MEMBER_EDITOR)) {
          homepageRoute = '/manager/workspace/membergroups';
        }
        if (profile.roles.includes(Role.EVENT_EDITOR)) {
          homepageRoute = '/manager/workspace/quizevents';
        }

        this.$store.commit('saveProfile', profile);
        this.$store.commit('setHomepage', homepageRoute);
        this.$router.push(homepageRoute);
      } catch (err) {
        if (err && err.response && err.response.status === 401) {
          this.loginMessage = '登入失敗，錯誤的帳號或密碼';
          this.reset();
          setTimeout(() => {
            this.loginMessage = '';
          }, 3000);
        } else {
          console.error(err);
        }
      }
    },
  },
});
